// dataStore.js
var DataStore = pc.createScript('dataStore');

var data ={
    country : "",
    city : "",
    type:"",
    employee:0
};
// initialize code called once per entity
DataStore.prototype.initialize = function(_d) {
    let r = new pc.Color(Math.random(),Math.random(),Math.random(),1);
    var material = new pc.BasicMaterial();
    material.color.set(Math.random(),Math.random(),Math.random());
    material.update();
    this.entity.model.model.meshInstances[0].material = material;
    
};
DataStore.prototype.getData = function() {
    return data;
};
// update code called every frame
DataStore.prototype.update = function(dt) {

};

